.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  word-break: break-word;
  padding: 1rem 0;
  border-bottom: 1px solid #D8E0E3; }
  .row:first-child {
    padding-top: 0; }
  .row:last-child {
    padding-bottom: 0;
    border-bottom: 0; }

.proxyMissing .text {
  color: #231536;
  margin-right: 6px; }

.proxyMissing .accountIcon :global(.account-icon-body) {
  fill: #708390; }

.proxyMissing .accountIcon :global(.account-icon-background) {
  fill: #fff; }

.proxyMissing .warningIcon :global(.warning-icon) {
  fill: #546978; }

.proxyAvailable .text {
  color: #1AAB9B;
  margin-right: 6px; }

.proxyAvailable .accountIcon {
  border-color: #1AAB9B; }
  .proxyAvailable .accountIcon :global(.account-icon-background) {
    fill: white; }
  .proxyAvailable .accountIcon :global(.account-icon-body) {
    fill: #1AAB9B; }

.proxyAvailable .warningIcon :global(.warning-icon) {
  fill: #1AAB9B; }

.warning .text {
  color: #97979B;
  font-size: .8125rem;
  line-height: 18px; }

.warning .warningIcon {
  height: 1.375rem;
  width: 1.375rem;
  flex-shrink: 0;
  margin-right: .8125rem;
  margin-left: 0; }
  .warning .warningIcon :global(.warning-icon) {
    fill: #97979B; }

.allowances .text {
  font-size: 0.875rem;
  color: #97979B; }

.text {
  font-size: .875rem; }

.accountIcon {
  width: 24px;
  height: 24px;
  margin-right: 1rem;
  border: 1px solid #D8E0E3;
  border-radius: 100%; }

.warningIcon {
  height: 18px;
  width: 18px;
  margin-left: 6px; }

.button {
  height: auto;
  font-size: 12px;
  font-weight: 600;
  padding: 6px 15px; }

.placeholder {
  min-height: 27px;
  display: flex;
  align-items: center; }



