:global(.instantTooltip.show) {
  opacity: 1;
  border: 1px solid #D8E0E3;
  background-color: #fff;
  font-size: 12px;
  padding: 6px 10px;
  color: #3F3F47;
  max-width: 200px;
  border-radius: 6px; }

:global(.instantTooltip.show p) {
  font-size: 12px;
  line-height: 18px;
  margin: 0; }

:global(.instantTooltip.show:after) {
  display: none;
  box-sizing: border-box;
  transform-origin: 0 0;
  transform: rotate(-45deg);
  width: 0;
  height: 0;
  border: 0.5em solid white !important; }

:global(.instantTooltip.show.place-top:after) {
  bottom: -12px; }

:global(.instantTooltip.show.place-right:after) {
  left: -9px; }

:global(.instantTooltip.show.place-left:after) {
  right: -4px; }

:global(.instantTooltip.show.place-bottom:after) {
  top: 0; }

.warningIcon {
  width: 17px;
  height: 17px;
  margin-right: auto; }
  .warningIcon.grey :global(.warning-icon) {
    fill: #708390; }
  .warningIcon.green :global(.warning-icon) {
    fill: #1AAB9B; }



