.taxExporterDescription {
  flex-grow: 1;
  font-size: 14px;
  display: block;
  margin-top: 8px;
  margin-bottom: 8px;
  color: #3F3F47; }
  @media (max-width: 360px) {
    .taxExporterDescription {
      font-size: 12px; } }
  .taxExporterDescription span {
    color: #231536;
    display: block;
    font-size: 15px;
    margin-bottom: 8px; }
    @media (max-width: 360px) {
      .taxExporterDescription span {
        font-size: 14px; } }

.taxExporterPanelBody {
  display: flex;
  width: 100%; }

.taxExporterButton {
  margin-top: 8px;
  padding: 5px .833em; }
  @media (max-width: 360px) {
    .taxExporterButton {
      margin-top: 0;
      align-self: center; } }

.progressIcon {
  position: relative;
  width: 14px;
  height: 14px;
  top: 2px;
  margin: 0 25px; }



