.muted {
  color: #546978; }

.infoLabel {
  color: #9FAFB9;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: 600; }

.buySellSpan {
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  display: block;
  width: 100%;
  text-transform: capitalize; }

.redSpan {
  color: #DC5D00; }

.greenSpan {
  color: #1AAB9B; }

.currency {
  white-space: nowrap; }
  .currency.highlight {
    font-weight: 600; }



