.block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  flex-grow: 1; }

.inline {
  display: inline-flex;
  align-items: center; }

.icon {
  display: inline-flex;
  margin-right: 0.5rem;
  color: #9FAFB9; }

.mainInfo {
  display: flex;
  align-items: center;
  color: #3F3F47; }

.annotate {
  margin-top: 0.8em;
  font-size: 14px;
  color: #87878C; }

.link {
  color: #448AFF;
  text-decoration: underline; }



