.infoIcon {
  color: #708390;
  border: 1px solid #708390;
  border-radius: 100%;
  width: 22px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  font-size: 12px;
  font-weight: bold; }

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.progressIcon {
  display: inline-block;
  border: 2.8px solid #9FAFB9;
  border-right-color: #ECF1F3;
  border-radius: 100%;
  width: 16px;
  height: 16px;
  animation: spin 3s linear infinite; }

.progressIconLight {
  border-color: #EEE;
  border-right-color: #448AFF; }

.progressIconSm {
  width: 12px;
  height: 12px; }

.progressIconLg {
  width: 26px;
  height: 26px; }

.socialIcon path {
  fill: #9FAFB9; }

.socialIcon:hover path {
  fill: #31424E; }

.btnIcon {
  height: 100%;
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center; }



