.radio {
  position: relative;
  display: block; }
  .radio input[type="radio"] {
    display: none;
    margin-right: 0;
    margin-top: 0;
    position: relative; }
    .radio input[type="radio"] + span:before {
      content: "";
      position: absolute;
      left: -30px;
      top: 2px;
      box-sizing: border-box;
      width: 20px;
      height: 20px;
      background-color: white;
      border: 1.3px solid #D4D9E1; }
    .radio input[type="radio"]:checked + span:before {
      border: 4px solid #1AAB9B; }
    .radio input[type="radio"]:disabled + span:before, .radio input[type="radio"]:read-only + span:before {
      cursor: none;
      pointer-events: none;
      background: #F6F8F9; }
  .radio input[type="radio"] + span:before,
  .radio input[type="radio"] + span:after {
    border-radius: 50%; }

.hasError {
  background-color: red; }



