.depthChartWithLoading {
  width: 508px; }
  @media (max-width: 992px) {
    .depthChartWithLoading {
      width: 100%; } }

.depthChart {
  position: relative;
  display: inline-block; }

.sellChart {
  fill: #F99374;
  background-color: #F99374; }

.sellChartDark {
  fill: #DC5D00;
  background-color: #DC5D00; }

.buyChart {
  fill: #1CBA80;
  background-color: #1CBA80; }

.buyChartDark {
  fill: #1AAB9B;
  background-color: #1AAB9B; }

.hidden {
  visibility: hidden; }

.axisMainLabel {
  font-weight: 700;
  color: #87878C; }

.axisLineMain {
  stroke: #D8E0E3;
  stroke-width: 2px; }

.axisLineAdditional {
  stroke: #D8E0E3;
  stroke-width: 1px; }

.axisYLineAdditional {
  stroke: #9FAFB9;
  stroke-width: 0.5; }

.axisYTokenLabel {
  font-weight: 600;
  color: #87878C;
  font-size: 10px; }

.infoBox {
  background-color: white;
  border: 1px solid #DC5D00;
  display: table;
  font-size: 10px;
  padding: 8px;
  position: absolute; }

.infoBoxRed {
  border-color: #DC5D00;
  stroke: #DC5D00; }

.infoBoxGreen {
  border-color: #1AAB9B;
  stroke: #1AAB9B; }

.sellText {
  color: #FF6951; }

.buyText {
  color: #1CBA80; }

.infoSummary {
  display: table-row; }

.infoSummaryLine {
  padding-top: 8px;
  padding-bottom: 8px;
  display: table-cell;
  white-space: nowrap; }
  .infoSummaryLine:first-child {
    padding-right: 10px; }

.infoSummaryLegendImg {
  margin-right: 8px;
  margin-top: 3px; }

.legendImg {
  height: 9px;
  width: 12px; }

.smallLegend {
  align-items: center;
  background-color: white;
  border: 1px solid #9FAFB9;
  border-radius: 2px;
  display: inline-flex;
  justify-content: flex-end;
  font-size: 10px;
  padding: 4px 6px;
  position: absolute; }

.dotGlow {
  fill: rgba(255, 255, 255, 0.1); }

.dotSell {
  fill: white;
  stroke-width: 1.5;
  stroke: #DC5D00; }

.dotBuy {
  fill: white;
  stroke-width: 1.5;
  stroke: #1AAB9B; }



