.fallBar,
.fallStick {
  fill: #FF6951;
  background-color: #FF6951; }

.riseStick {
  fill: #1AAB9B;
  background-color: #1AAB9B; }

.riseBar {
  fill: #E8F8F2;
  background-color: #E8F8F2;
  stroke: #1AAB9B;
  stroke-width: 1; }

.equal {
  fill: #87878C;
  background-color: #87878C; }

.volume {
  fill: #ECF1F3;
  background-color: #ECF1F3; }

.bands rect {
  fill: darkgrey;
  fill-opacity: 0.3;
  stroke-opacity: 0;
  pointer-events: all;
  shape-rendering: crispEdges; }

.bands .hoved {
  fill-opacity: .6; }

:global(.candles) .hoved {
  stroke-width: 2; }
  :global(.candles) .hoved.riseBar {
    stroke: #00c5af; }
  :global(.candles) .hoved.fallBar {
    stroke: #FFA799; }
  :global(.candles) .hoved.equal {
    stroke: #87878C; }

:global(.sticks) .hoved.riseStick {
  stroke: #06bfaa; }

:global(.sticks) .hoved.fallStick {
  stroke: #FFA799; }

:global(.volume) .hoved {
  fill: #58585F; }

.axisYMainLabel {
  font-weight: 600;
  font-size: 12px;
  color: #708390; }

.axisYVolumeLabel {
  color: #708390;
  font-size: 12px; }

.axisXMainLabel {
  color: #708390;
  font-size: 12px; }

.axisLineMain {
  stroke: #D8E0E3;
  stroke-width: 1px; }

.axisYVolumeLineMain {
  stroke-linecap: round; }

.axisLineGrid {
  stroke: #D4D9E1;
  stroke-width: 1px; }

.infoBox {
  position: absolute;
  left: auto;
  top: 0;
  background-color: white;
  font-size: 10px;
  padding: 2px 3px;
  display: flex;
  justify-content: space-between;
  width: calc(100% - 20px);
  max-width: 440px; }

.infoBoxItem {
  width: 200px; }
  @media (max-width: 480px) {
    .infoBoxItem {
      width: auto; } }
  @media (max-width: 360px) {
    .infoBoxItem {
      font-size: 9px; } }



