.borderBox {
  border: 1px solid #D8E0E3;
  border-radius: 4px; }

.smPadding {
  padding: 10px 12px; }

.mdPadding {
  padding: 14px; }

.nonePadding {
  padding: 0; }

.hr {
  border: none;
  border-bottom: 1px solid;
  clear: both;
  width: 100%;
  margin: 0; }

.lightHr {
  border-color: #D8E0E3; }

.darkHr {
  border-color: #D8E0E3; }



